@import "_mixins/em";
@import "vars";
@import "libs";
@import "base";
::placeholder {
	color: #666;
} // ::placeholder

::selection {
	background-color: $accent;
	color: #fff;
} // ::selection
html {
  overflow-x: hidden;
} // html
body {
	font-size: 16px;
	min-width: 320px;
	position: relative;
	line-height: 1.65;
	font-family: $default-font;
	overflow-x: hidden;
	padding: 0;
	margin: 0;
  color: $text;
  background: #FDFAEE;
	input, textarea {
		border: #666 1px solid;
		outline: none;
		&:focus:required:invalid {
			border-color: red;
		} // body input, textarea:focus:required:invalid
		&:required:valid {
			border-color: green;
		} // body input, textarea:required:valid
	} // body input, textarea
} // body

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
	&.img-width {
		width: 100%;
	} // .img-responsive.img-width
} // .img-responsive

* {
	box-sizing: border-box;
} // *

.text-center {
	text-align: center;
} // .text-center


.header {
  padding-top: 20px;
  padding-bottom: 10px;
  width: 100vw;
  height: auto;
  &__logo{
    width: 100%;
  } // .header__logo
  &__left{ 
    width: 20%;
    @media (max-width: 1001px) {
      display: none;
    }
  } // .header__left
  &__right{
    width: 80%;
    @media (max-width: 1001px) {
      width: 90%;
    }
    @media (max-width: 1001px) {
      width: 100%;
    }
  } // .header__right
  &__contacts {
    &> .flex {
      @media (max-width: 701px) {
        justify-content: flex-start;
        flex-wrap: wrap;
      }
      .header__contacts-item {
        &:first-child {
          @media (max-width: 1001px){
            width: calc(48.2% - 10px);
          }
          @media (max-width: 701px){
            width: 100%;
            margin: 0;
            order: 3;
          }
        } // .header__contacts> .flex .header__contacts-item:first-child
        &:nth-child(2) {
          @media (max-width: 1001px) {
            width: calc(30% - 10px);
          }
          @media (max-width: 701px){
            width: calc(50% - 10px);
          }
        } // .header__contacts> .flex .header__contacts-item:nth-child(2)
        &:nth-child(3) {
          @media (max-width: 1001px) {
            width: calc(26.8% - 10px);
          }
          @media (max-width: 701px){
            width: calc(50% - 10px);
          }
        } // .header__contacts> .flex .header__contacts-item:nth-child(3)
        // @media (max-width: 1200px) {
        //   padding: 0 5px;
        //   width: calc(35% - 10px);
        // } // .header__contacts> .flex .header__contacts-item // @media (max-width: 1200px)
      } // .header__contacts> .flex .header__contacts-item
    } // .header__contacts> .flex

    &-item{
      padding: 0 15px;
      font-size: 16px;
      @media (max-width: 1200px) {
        padding: 5px;
        font-size: 14px;
      }
      @media (max-width: 701px){
        font-size: 12px;
      }
    } // .header__contacts-item
    &-icon{
      width: 19px;
      height: auto;
      @media (max-width: 701px){
        width: 15px;
        img {
          max-width: 100%;
        } // .header__contacts-icon img
      }
    } // .header__contacts-icon
    &-link {
      line-height: 1;
      color: $text;
      text-decoration: none;
      &:hover{
        text-decoration: underline;
      } // .header__contacts-link:hover
      span {
        display: block;
      } // .header__contacts-link span
    } // .header__contacts-link
    &-button {
      font-size: em(12);
      padding: 12px 30px;
      @media (max-width: 1200px) {
        padding: 12px 14px;
      }
    } // .header__contacts-button
  } // .header__contacts
} // .header

.menu{
  background: $brown;
  position: relative;
  margin: 15px 0;
  &__desctop {
    @media (max-width:1000px) {
      display:none;
    }
  }
  &__mobile {
    display: none;
    @media (max-width:1000px) {
      display:block;
    }
    &--container{
      max-height: 0;
      overflow: hidden;
      transition: all .3s;
      &.active {
        max-height: 500px;
      }
    }
    .logo {
      max-height: 50px;
      width: 15%;
      &__img {
        max-width: 100%;
        transform: scale(1.5, 1.5);
      }
    }
    .burger {
      width: 25px;
      height: 17px;
      position: relative;
      span{
        width: 100%;
        transition: all .3s;
        height: 3px;
        position: absolute;
        background: #fff;
        &:nth-child(1) {
          top: 0;
        }
        &:nth-child(2) {
          top: 50%;
          transform: translateY(-50%);
        }
        &:nth-child(3) {
          bottom: 0;
        }
      }
      &.active {
        span{
          &:nth-child(1) {
            top: 0;
            opacity: 0;
          }
          &:nth-child(2) {
            top: 50%;
            transform: translateY(-50%) rotate(45deg);
          }
          &:nth-child(3) {
            top: 50%;
            bottom: initial;
            transform: translateY(-50%) rotate(-45deg);
          }
        }
      }
    }
  }
  &:before{
    content: '';
    z-index: -1;
    background:$brown;
    height: 100%;
    width: 200vw;
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    top: 0;
    bottom: 0;
  } // .menu:before
  &__container {
    list-style: none;
    margin: 0;
    padding: 0;
  } // .menu__container
  &__item{
    flex-grow: 1;
  } // .menu__item
  &__link {
    display: block;
    color: #fff;
    text-align: center;
    text-decoration: none;
    transition: all .2s;
    line-height: em(62);
    &:hover {
      background: $wheat;
      color: $text;
    } // .menu__link:hover
  } // .menu__link
} // .menu

.search {
  position: relative;
  width: 80%;
  &__input {
    background:transparent;
    height: 50px;
  } // .search__input
  &__button {
    position: absolute;
    top: 50%;
    height: 100%;
    right: 0;
    width: 50px;
    transform: translateY(-50%);
    img {
      display: block;
      max-width: 100%;
      margin: auto;
    } // .search__button img
  } // .search__button
} // .search
.logo{
  img{
    max-width: 100%;
  } // .logo img
} // .logo
.minicart {
  width: 8%;
  cursor: pointer;
  position: relative;
  margin-left: 30px;
  background: transparent;
  border: none;
  @media (max-width: 1001px) {
    margin-left: 5px;
    width:20%;
    padding: 0;
  }
  span {
    display: block;
  } // .minicart span
  &__icon-img{
    width: 35px;
    display: block;
    height: auto;
    @media (max-width: 1001px) {
      width: 25px;
    }
  } // .minicart__icon-img
  &__currency {
    border: .5px solid #FFCCC5;
    background: $softpink;
    line-height: 1;
    padding: 4px 10px;
    font-size: 14px;
    border-radius: 200px;
    position: absolute;
    top: -10px;
    right: -17px;
    @media (max-width: 1001px) {
      font-size: 12px;
      padding: 2px 6px;
      right: -13px;
    }
  } // .minicart__currency
} // .minicart

.promoslider{
  margin: 10px 0;
  &__slider{

    &-row {
      min-height: 600px;
      @media (max-width: 1200px) {
        min-height: 500px;
      }
      @media (max-width: 701px) {
        min-height: 350px;
      }
    } // .promoslider__slider-row
  } // .promoslider__slider
  &__content {
    width: 100%;
  } // .promoslider__content
  &__button{
    padding: 14px 20px;
  } // .promoslider__button
  &__right{ 
    transition: all .3s;
    transition-delay: .3s;
    overflow: hidden;
    width: 35%;
    position: relative;
    background:transparent!important;
    @media (max-width: 1200px) {
      width: 45%;
      padding: 0 15px;
    }
    @media (max-width: 1001px) {
      width: 55%;
    }
    @media (max-width: 701px) {
      width: 100%;
    }
    &-bg{
      position: absolute;
      z-index: 2;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      opacity: 0;
      transition: all .3s;
      transition-delay: 2s;
    } // .promoslider__right-bg
  } // .promoslider__right
  &__title{
    margin: 0;
  } // .promoslider__title
  &__description{
    font-size: em(18);
    letter-spacing: 0.02em;
    line-height: 160%;
    margin: 0;
    padding: 70px 0;
    @media (max-width: 1200px) {
      padding: 35px 0;
    }
    @media (max-width: 701px) {
      padding: 10px 0;
    }
  } // .promoslider__description
  &__left {
    width: 65%;
    transition: all .3s;
    overflow: hidden;
    position: relative;
    @media (max-width: 1200px) {
      width: 55%;
    }
    @media (max-width: 1001px) {
      width: 45%;
    }
    @media (max-width: 701px) {
      display: none;
    }
  } // .promoslider__left
  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    min-height: 600px;
    opacity: 0;
    transition: all .3s;
    transition-delay: 2s;
  } // .promoslider__image
  &__info {
    opacity: 0;
    position: relative;
    z-index: 4;
    transition: all .3s;
    transition-delay: 2s;
    @media (max-width: 701px) {
      padding: 50px 0;
      font-size: 14px;
    }
    @media (max-width: 1001px) {
      font-size: 14px;
    }
  } // .promoslider__info
  &__slice {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    background: #000;
    max-width: 0;
    z-index: 999;
    left: 0;
  } // .promoslider__slice
  .slider__dot{
    width: 10px;
    height: 10px;
    margin: 0 5px;
    border: 1px solid #FFFFFF;
    border-radius: 50%;
    transition: all .3s;
    &.active{
      background: #fff;
    } // .promoslider .slider__dot.active
  } // .promoslider .slider__dot
  &-skip {
    width: 65%;
    @media (max-width: 1200px) {
      width: 55%;
    }
    @media (max-width: 1001px) {
      width: 45%;
    }
    @media (max-width: 701px) {
      display: none;
    }
  } // .promoslider-skip
  &__controls {
    width: 35%;
    @media (max-width: 1200px) {
      width: 45%;
    }
    @media (max-width: 1001px) {
      width: 55%;
    }
    @media (max-width: 701px) {
      width: 100%;
    }
    &-container{
      width: 100%;
      z-index: 1000;
      height: 50px;
      bottom: 0;
      position: absolute;
    } // .promoslider__controls-container
  } // .promoslider__controls
  &__item{
    .flex {
      height: 100%;
    } // .promoslider__item .flex
    &.visible {
      .promoslider {
        &__right{
          .promoslider__slice {
            animation: slisexPromo 2s 1s alternate;
          } // .promoslider__item.visible .promoslider__right .promoslider__slice
          &-bg {
            transition-delay: 2s;
            opacity: 1;
          } // .promoslider__item.visible .promoslider__right-bg
        } // .promoslider__item.visible .promoslider__right
        &__info {
          transition-delay: 2s;
          opacity: 1;
        } // .promoslider__item.visible .promoslider__info
        &__image {
          transition-delay: 1s;
          opacity: 1;
        } // .promoslider__item.visible .promoslider__image
        &__left{
          .promoslider__slice {
            animation: slisexPromo 2s alternate;
          } // .promoslider__item.visible .promoslider__left .promoslider__slice
        } // .promoslider__item.visible .promoslider__left
      } // .promoslider__item.visible .promoslider
    } // .promoslider__item.visible
  } // .promoslider__item
} // .promoslider

@keyframes slisexPromo {
  0% {
    max-width: 0%;
    left: 0;
  } // @keyframes slisexPromo 0% } 
  50% {
    max-width: 100%;
    left: 0;
  } // @keyframes slisexPromo 0% } 
  51% {
    left: initial;
    right: 0;
  } // @keyframes slisexPromo 0% } 
  100% {
    right: 0;
    left: initial;
    max-width: 0%;
  } // @keyframes slisexPromo 0%
} // @keyframes slisexPromo

.sampletab{
  margin: 40px 0;
  &__buttons {
    &-icon {
      width: 31px;
      height: 31px;
      margin-left: 15px;
      display: none;
      @media (max-width: 1001px) {
        width: 20px;
        height: 20px;
        margin-left: 10px;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      } // .sampletab__buttons-icon img
    } // .sampletab__buttons-icon
    &-item{
      width: calc(25% - 6px);
      padding: 20px 0;
      margin: 0 3px;
      background: $wheat;
      text-transform: uppercase;
      text-align: center;
      @media (max-width: 1001px) {
          width: calc(50% - 6px);
          margin-bottom: 10px;
      }
      @media (max-width: 701px) {
          width: 100%;
          padding: 10px 0;
          font-size: 14px;
      }
      &.active {
        .sampletab__buttons-icon{
          display: block;
        } // .sampletab__buttons-item.active .sampletab__buttons-icon
        background:$brown;
        color: #fff;
      } // .sampletab__buttons-item.active
    } // .sampletab__buttons-item
    &-text,
    &-icon,
    &-icon img{
      pointer-events: none;
    } // .sampletab__buttons-icon img
  } // .sampletab__buttons
  &__content {
    @media (max-width: 1001px) {
      flex-wrap: wrap;
      .sampletab__product:nth-child(3n) {
        display: none;
      } // .sampletab__content .sampletab__product:nth-child(3n)
    }
    @media (max-width: 701px) {
      .sampletab__product:nth-child(3n) {
        display: block;
      } // .sampletab__content .sampletab__product:nth-child(3n)
    }
  } // .sampletab__content
  &__product{
    margin: 0 3px;
    margin-top: 30px;
    @media (max-width: 1001px) {
      width: calc(33% - 6px);
    }
    @media (max-width: 701px) {
      width: calc(50% - 6px);
      margin-top: 6px;
    }
  } // .sampletab__product
} // .sampletab

.usefullinfo {
  &__item{
    width: calc(33.333% - 10px);
    margin: 5px 5px;
    position: relative;
    object-fit: cover;
    max-height: 430px;
    @media (max-width: 1200px) {
      width: calc(30% - 10px);
    }
    @media (max-width: 1001px) {
      font-size: 12px;
    }
    @media (max-width: 701px) {
      width: calc(50% - 10px);
      font-size: 10px;
      min-height: 266px;
    }
  } // .usefullinfo__item
  &__container{
    font-size: 18px;
    @media (max-width: 1200px) {
      font-size: 16px;
    }
    @media (max-width: 701px) {
      flex-wrap: wrap;
    }
  } // .usefullinfo__container
  &__delivery{
    padding: 109px 35px;
    border: 1px solid $brown;
    @media (max-width: 1200px) {
      padding: 67px 20px 24px;
      width: calc(40% - 10px);
    }
    @media (max-width: 1001px) {
      padding: 67px 10px 24px;
    }
    @media (max-width: 701px) {
      width: calc(100% - 10px);
    }
    &-title{
      text-transform: uppercase;
      font-size: em(24, 18);
      margin: 0;
      padding: 0;
      padding-bottom: 24px;
      position: relative;
      &:before {
        content: "";
        background-image: url(../img/lines/text-zigzag.svg);
        width: 100px;
        height: 13px;
        position: absolute;
        background-size: contain;
        background-repeat: no-repeat;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
      } // .usefullinfo__delivery-title:before
      @media (max-width: 1200px) {
        padding-bottom: 16px;
      }
    } // .usefullinfo__delivery-title
    &-text{
      letter-spacing: 0.02em;
      font-size: em(18, 18);
      padding: 0;
      margin: 0;
      margin: 36px 0;
      @media (max-width: 1200px) {
        margin: 15px 0;
      }
    } // .usefullinfo__delivery-text
  } // .usefullinfo__delivery
  &__info{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  } // .usefullinfo__info
  &__title{
    font-size: em(20);
    padding: 34px 21px;
    margin: 0;
    padding-bottom: 21px;
    margin-bottom: 49px;
    position: relative;
    @media (max-width: 1200px) {
      margin-bottom: 10px;
    }
    @media (max-width: 1001px) {
      padding: 15px 10px;
    }
    &:before {
      content: '';
      background-image: url(../img/lines/usefull__line.svg);
      width: 102px;
      height: 10px;
      position: absolute;
      bottom: 0;
      z-index: 2;
      background-position: bottom left;
      left: 0;
      background-size: cover;
    } // .usefullinfo__title:before
  } // .usefullinfo__title
  &__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  } // .usefullinfo__img
} // .usefullinfo

.aboutus {
