  &__button{
    padding: 0 20px;
    @media (max-width: 701px) {
      padding: 0 10px;
    }
  } // .footer__button
} // .footer

.card-product {
  background: #fff;
  padding: 30px 0;
  &__mobiledescr{
    border-bottom: .5px solid $brown;
    border-top: .5px solid $brown;
    padding: 10px 0;
    @media (min-width: 701px) {
      display: none;
    }
    &-title {
      line-height: 1;
    }
    &-text {
      overflow: hidden;
      max-height: 0;
      margin: 0;
      transition: all .8s;
      padding: 0;
    }
    &.active {
      .card-product__mobiledescr-text {
        max-height: 500px;
      }
    }
  }
