.slider{
    width: 100%;
    position: relative;
    &__row {
        position: relative;
    }
    &__item {
        position: absolute;
        top: 0;
        height: 100%;
        width: 100%;
        &--multiple {
            float: left;
        }
    }
    &__controls {
        position: relative;
        z-index: 999;
        button {
            background: none;
            border: none;
            cursor: pointer;
            display: block;
        }
    }
}