@import "_mixins/em";

.container {
    width: 1360px;
    margin: 0 auto;
    padding: 0 15px;
    position: relative;
    @media (max-width: 1366px) {
        width: 1200px;
    }
    @media (max-width: 1200px) {
        width: 1000px;
    }
    @media (max-width: 1001px) {
        width: 700px;
    }
    @media (max-width: 701px) {
        width: 100%;
    }
}

.product {
    &__item{
      font-size: 16px;
      width: calc(25% - 6px);
      background: #fff;
      margin: 15px 3px;
      @media (max-width: 1200px) {
        font-size: 14px;
      }
      @media (max-width: 1001px) {
        width: calc(33% - 6px);
        font-size: 12px;
      }
      @media (max-width: 701px) {
        width: calc(50% - 6px);
        font-size: 10px;
        margin: 6px;
      }
    } // .product__item
    &__button {
      width: 100%;
      height: 50px;
      background: $softpink;
      border: none;
      cursor: pointer;
      text-transform: uppercase;
      transition: all .3s;
      position: relative;
      @media (max-width: 701px) {
        height: 30px;
      }
      &:before {
        content: '';
        width: 25px;
        z-index: 5;
        height: 2px;
        transition: all .3s;
        transition-delay: .3s;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        background: transparent;
      } // .product__button:before
      &:after {
        content: '';
        width: 25px;
        z-index: 5;
        height: 2px;
        transition: all .3s;
        transition-delay: .3s;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        background: transparent;
      } // .product__button:after
      &:hover{
        color: #fff;
        background:$brown;
        &:before,
        &:after {
          background: #fff;
        } // .product__button:hover:after
      } // .product__button:hover
    } // .product__button
    &__image{
      display: block;
      img{
        width: 100%;
        height: 280px;
        object-fit: cover;
        @media (max-width: 1200px) {
          height: 200px;
        }
        @media (max-width: 1001px) {
            height: 150px;
        }
        @media (max-width: 701px) {
            height: 100px;
        }
      } // .product__image img
    } // .product__image
    &__title{
      font-size: em(18);
      display: block;
      color: $brown;
      text-decoration: none;
    } // .product__title
    &__info{
      padding: 15px 15px;
      @media (max-width: 701px) {
        padding: 5px;
    }
    } // .product__info
    &__spec{
      padding: 0;
      margin: 5px 0;
      list-style: none;
      &-title{
      } // .product__spec-title
    } // .product__spec
    &__price{
      font-size: em(20, 0);
    } // .product__price
  } // .product
.flex {
    display: flex;
    &-align{
        &-start {
            align-items: flex-start;
        }
        &-center {
            align-items: center;
        }
        &-end {
            align-items: flex-end;
        }
    }
    &-wrap {
        flex-wrap: wrap;
    }
    &-justify {
        &-between {
            justify-content: space-between;
        }
        &-start {
            justify-content: flex-start;
        }
        &-center {
            justify-content: center;
        }
        &-end {
            justify-content: flex-end;
        }
    }
}

.font {
    &-bold {
        font-weight: bold;
    }
    &-medium {
        font-weight: 500;
    }
    &-semi {
        font-weight: 600;
    }
    &-light {
        font-weight: 300;
    }
    &-rubik {
        font-family: "Rubik", sans-serif;
    }
    &-default {
        font-family: "Montserrat", sans-serif;
    }
    &-normal{
        font-weight: normal;
    }
}

p {
    font-size: em(16);
}

h4 {
    font-size: em(20);
    font-weight: normal;
    b {

    }
}
h1.page-title, h2.section-title {
    font-size: em(36);
    font-weight: normal;
    @media (max-width: 1200px) {
        font-size: em(24);
    }
    @media (max-width: 701px) {
        font-size: em(20);
    }
}

button.button, a.button {
    color: $text;
    text-decoration: none;
    cursor: pointer;
    text-transform: uppercase;
    background: none;
    border: none;
    display: inline-block;
    &--white{
        background: #fff;
    }
    &--pink {
        background:$softpink;
    }
    &--fill {
        color: #4B392C;
        &.button--dark {
            border: 1px solid #BA9A86;
        }
        &.button--pink {
            border: 1px solid $softpink;
        }
        &.button--brown {
            border: 1px solid $brown;
        }
    }
}

.form{ 
    &-input {
        background: #fff;
        border: 1px solid #BA9A86;
        width: 100%;
        padding: 0 10px;
        display: block;
        &--white{
            border-color: #fff;
        }
        &--line {
            background: none;
        }
    }
    &-label{
        display: block;
        width: 100%;
    }
    &-textarea {
        background: #fff;
        border: 1px solid #BA9A86;
        width: 100%;
        padding: 10px 10px;
    }
}

.page__header {
    &-title {
        margin-bottom: 0;
    }
    padding-bottom: 30px;
    @media (max-width: 701px) {
        padding-bottom: 5px;
    }
}
.breadcrumb{
    text-transform: uppercase;
    margin: 10px 0;
    padding: 0;
    list-style: none;
    .breadcrumb-item {
        padding: 0 13px;
        font-size: em(13);
        position: relative;
        &.active {
            opacity: .8;
        }
        a {
            color: $text;
            text-decoration: none;
            &:hover {
                text-decoration: underline;
            }
        }
        &:before{
            content: '';
            width: 8px;
            height: 2px;
            position: absolute;
            left: -4px;
            top: 50%;
            transform: translateY(-50%);
            background: $text;
        }
        &:first-child {
            padding-left: 0;
            &:before {
                display: none;
            }
        }
    }
}